export default function SpecialityTourCard({ tour }) {
  return (
    <div className="relative rounded-xl overflow-hidden shadow group transition hover:shadow-lg bg-white">
      {/* Discount Badge */}
      {tour.discount && (
        <span className="absolute top-3 left-3 bg-[#FF914D] text-white text-xs font-semibold px-3 py-1 rounded z-10 shadow">
          {tour.discount}
        </span>
      )}
      {/* Image */}
      <img
        src={tour.image}
        alt={tour.title}
        className="w-full h-64 object-cover"
      />
      {/* Overlay Content */}
      <div className="absolute inset-0 flex flex-col justify-end p-6 bg-gradient-to-t from-black/70 via-black/10 to-transparent">
        <h3 className="font-bold text-3xl mb-2 text-white drop-shadow">{tour.title}</h3>
        <div className="flex items-center gap-2 mb-4">
          <img src="/icons/flight.png" alt="flight" className="w-5 h-5" />
          <span className="text-white text-lg font-bold">+</span>
          <img src="/icons/hotel.png" alt="Hotel" className="w-5 h-5" />
        </div>
        <button className="border border-white text-white rounded px-5 py-1 font-semibold bg-black/30 hover:bg-white hover:text-black transition w-fit">
          Explore
        </button>
      </div>
    </div>
  );
}